.teambackdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);

  opacity: 1;
  transition: opacity 500ms cubic-bezier(0.4, 0, 0.2, 1);
}
.teambackdrop.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}
.teambackdrop.is-hidden .modal-footer {
  transform: translate(-50%, -50%) scale(1.1);
}
.modal-footer {
  margin: auto;
  width: 100%;
  padding-left: 20px;
  padding-right: 20px;

  position: absolute;
  max-height: 100%;

  top: 50%;
  left: 50%;
  color: #545454;
  padding: 20px;
  border-radius: 7px;
  border-color: #ff6b08;
  border-style: solid;
  overflow: scroll;

  border: none;
  box-shadow: 0px 0px 40px #ff6b08, 0px 1px 10px rgb(252, 229, 22),
    0px 2px 2px rgba(0, 0, 0, 0.12);

  background-color: #f7f7f7;
  transform: translate(-50%, -50%) scale(1);
  transition: transform 500ms cubic-bezier(0.4, 0, 0.2, 1);

  &__btn-close {
    position: absolute;
    top: 8px;
    right: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 30px;
    height: 30px;
    padding: 0;
    background-color: transparent;
    border: transparent;
    cursor: pointer;

    &:hover,
    &:focus {
      fill: #ff6b08;
    }
  }
  &__name-team {
    font-size: 30px;

    align-items: center;
    text-align: center;
    color: #ff6b08;
  }
  &__icon-close {
    fill: #ff6b08;
  }
}

@media screen and (min-width: 768px) {
  .modal-footer {
    width: 100%;
  }
}
@media screen and (min-width: 1024px) {
  .modal-footer {
    max-width: 1024px;
    // overflow: hidden;
  }
}
